<template>
    <el-container style="height: 100vh; width:100vw;  border: 1px solid #eee">
        <el-container>
            
            <el-main>
                  <div v-if='tableData.length>0 '  
     width="100vw"
     height='100vh'>
    <el-table
    border
    @row-click	= 'rowClick'
    :data="tableData.filter(data => !search || data.patientName.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%">
      <el-table-column     align='center' label="patientId" prop="patientId"></el-table-column>
      <el-table-column     align='center'  label="patientName" prop="patientName"></el-table-column>
      <el-table-column     align='center' label="patientAge" prop="patientAge"></el-table-column>
      <el-table-column     align='center' label="patientMobile" prop="patientMobile"></el-table-column>
      <el-table-column     align='center' label="patientSex" prop="patientSex">
         <template slot-scope="scope">
           {{scope.patientSex == 1?'male':'female'}} 
           <!-- 1-male  2-female -->
         </template>
      </el-table-column>
      <el-table-column     align='center' label="patientStatus" prop="patientStatus">
        <template slot-scope="scope">
          <el-tag v-if='scope.row.patientStatus == 0'>normal</el-tag>
          <el-tag v-else-if="scope.row.patientStatus == 1" type="warning">suspect</el-tag>
          <el-tag v-else-if="scope.row.patientStatus == 2" type="danger">confirm</el-tag>
          <el-tag v-else-if="scope.row.patientStatus == 3" type="success">cured</el-tag>
          <el-tag v-else type="info">canceel</el-tag>
        </template>
      </el-table-column>
      <el-table-column     align='center' label="patientAddress" prop="patientAddress"></el-table-column>

      <el-table-column    align='center' label="doctorName" prop="doctorName"></el-table-column>
      <el-table-column    align='center' label="doctorTel" prop="doctorTel"></el-table-column>
      <el-table-column    align='center' label="doctorHospitalAddress" prop="doctorHospitalAddress"></el-table-column>

      <el-table-column  align='right' width="300">
        <template slot="header" slot-scope="scope">
          <el-input
            v-model="search"
            size="mini"
            placeholder="search by patientName"/>
        </template>

        <template slot-scope="scope">
          <el-button
            size="mini"
            type="danger"
            v-if="scope.row.patientStatus == 1"
            @click.stop="confirm(scope.$index, scope.row)">{{opera.confirm}}</el-button>
          <el-button
            size="mini"
             v-if="scope.row.patientStatus == 1"
            @click.stop="normal(scope.$index, scope.row)">{{opera.normal}}</el-button>

          <el-button
            size="mini"
            type="success"
             v-if="scope.row.patientStatus == 2"
            @click.stop="cured(scope.$index, scope.row)">{{opera.cured}}</el-button>
        </template>
      </el-table-column>
              <!-- 只有状态是1 疑似的时候才展示
          v-if='scope.patientStatus == 1' -->

  </el-table>
      <el-pagination
          style="padding:10px"
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage1"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="currentPageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalLen">
    </el-pagination>


      <el-dialog title="patientDetail" :visible.sync="dialogFormVisible">
      <el-form  :model="form"  >
        <el-form-item label="patientId" :label-width="formLabelWidth">
            <span>{{form.patientId}}</span>
        </el-form-item>
        <el-form-item label="patientName" :label-width="formLabelWidth">
            <span>{{form.patientName}}</span>
        </el-form-item>
        <el-form-item label="patientAge" :label-width="formLabelWidth">
            <span>{{form.patientAge}}</span>
        </el-form-item>
        <el-form-item label="patientMobile" :label-width="formLabelWidth">
            <span>{{form.patientMobile}}</span>
        </el-form-item>
        <el-form-item label="patientSex" :label-width="formLabelWidth">
            <span>{{form.patientSex == 1? 'male' :'female'}}</span>
        </el-form-item>
        <el-form-item label="patientStatus" :label-width="formLabelWidth">
            <span>{{form.patientStatus}}</span>
        </el-form-item>
        <el-form-item label="doctorName" :label-width="formLabelWidth">
            <span>{{form.doctorName}}</span>
        </el-form-item>
        <el-form-item label="doctorTel" :label-width="formLabelWidth">
            <span>{{form.doctorTel}}</span>
        </el-form-item>
        <el-form-item label="doctorHospitalAddress" :label-width="formLabelWidth">
            <span>{{form.doctorHospitalAddress}}</span>
        </el-form-item>


      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogFormVisible = false">ok</el-button>
      </div>
      </el-dialog>


  </div>
            </el-main>
  </el-container>
</el-container>
</template>
  

<script>

const urls = {
  //  change status post
  confirm:'/admin/updatePatientToConfirm',  //done 
  normal: '/admin/updatePatientToNormal',  //done 
  cured: '/admin/updatePatientToCured',//patientId pass to backend   //done 
  //get lists  or filter by name  post
  getList : '/admin/getPatientListForPages', //done 
  //get  getSinglePatient  post 
  getSinglePatient : '/admin/getPatientDetailById'
}

export default {
    data() {
      return {
        dialogFormVisible:false,
        formLabelWidth:'120px',
        tableData: [],
        search: '',
        opera:{
          confirm: 'confirm',
          normal:'normal',
          cured :'cured'
        },
        currentPage1: 1,
        currentPageSize:50,
        totalLen:0,
        form:{
          patientId:'1',
          patientName:'3',
          patientAge:'4',
          patientMobile:'',
          patientSex:'',
          patientStatus:'',
          doctorName:'',
          doctorTel:'',
          doctorHospitalAddress:''
        }
      }
    },
    created(){
        this.getUsers();
    },
    methods: {
      getUsers(){
        let postConfig = {
            "currentPage": String(this.currentPage1),
            "pageSize" :String( this.currentPageSize)
        }
      
        this.$http.post(urls.getList ,postConfig)
                .then(e=>{
                  let {result, totalCount} = e;
                  this.tableData = result;
                  this.totalLen = totalCount;
                })
                .catch(err=>{
                  console.log(err)
                })
      },
      changeStatus(type , patientId){
        let cfg = {
          patientId
        }
        let url = urls[type]
        this.$http.post(url,cfg)
                  .then(res=>{
                    if(res.data.flag ==1){
                      this.getUsers();
                    }
                  })
      },
      confirm(index, row) {
        this.changeStatus('confirm', row.patientId);
      },
      normal(index, row) {
        this.changeStatus('normal', row.patientId);
      },
      cured(index, row){
        this.changeStatus('cured', row.patientId);
      },
      rowClick(row){
        // console.log(row)
        this.dialogFormVisible = true;
        this.form = row
      },
      handleSizeChange(val) {
        if(this.search){
          this.search = ''
        }
        this.currentPageSize = val 

        this.getUsers()
      
      },
      handleCurrentChange(val) {
        if(this.search){
          this.search = ''
        }
        this.currentPage1 = val
        this.getUsers()
      }
    },
  }
</script>